    border-radius: 30px;
    /* margin-bottom: 10px; */
	margin-top: 30px;
	background: #cadbe7;
	background-image: -webkit-linear-gradient( left, #cadbe7, #edf3f7);
	background-image: linear-gradient( to right, #cadbe7, #edf3f7);
}

.speech-bubble::after {
    content: '';
	position: absolute;
	left: 0;
	top: 50%;
	width: 0;
	height: 0;
	border: 24px solid transparent;
	border-right-color: #cadbe7;
	border-left: 0;
	border-top: 0;
	margin-top: -12px;
	margin-left: -24px;
	/* background: #cadbe7;
	background-image: linear-gradient( #cadbe7, #edf3f7); */
}

.speech-bubble:nth-last-child(even) {
	position: relative;
	background:none !important;
}

